// @import "~bulma/bulma"
@import "~bulma/sass/utilities/_all";
@import "~bulma/sass/base/_all";
@import "~bulma/sass/grid/columns";
@import "~bulma/sass/elements/icon";
@import "~bulma/sass/elements/image";
@import "~bulma/sass/elements/box";
@import "~bulma/sass/elements/title";
@import "~bulma/sass/elements/container";
@import "~bulma/sass/layout/section";
@import "~bulma/sass/elements/button";
html {
    --chromecolor-light0: hsl(0, 0%, 100%);
    --chromecolor-light1: hsl(0, 0%, 97%);
    --chromecolor-light2: hsl(0, 0%, 97%);
    --chromecolor-dark0: hsl(0, 0%, 0%);
    --chromecolor-dark1: hsl(0, 0%, 2%);
    --chromecolor-dark2: hsl(0, 0%, 7%);

    --theme-style: 'modern';

    --text--color-base--light:            22, 21, 30;
    --background--color-base--light:      255,255,255;

    --text--color-base--dark:             254, 248, 239;
    --background--color-base--dark:       22, 21, 30;

    --systemBlueLight:   #2f67a5;
    --systemGrayLight:   rgb(142,142,147);
    --systemGreenLight:  #008221;
    --systemIndigoLight: rgb(88,86,214);
    --systemOrangeLight: rgb(255,149,0);
    --systemPinkLight:   rgb(255,45,85);
    --systemPurpleLight: rgb(175,82,222);
    --systemRedLight:    #f72c20;
    --systemTealLight:   rgb(1, 157, 228);
    --systemYellowLight: rgb(255,204,0);

    --systemBlueDark:    #51a8ff;
    --systemGrayDark:    rgb(152,152,157);
    --systemGreenDark:   rgb(48,209,88);
    --systemIndigoDark:  #9998ff;
    --systemOrangeDark:  rgb(255,159,10);
    --systemPinkDark:    rgb(255,55,95);
    --systemPurpleDark:  #fd6fce;
    --systemRedDark:     rgb(255,69,58);
    --systemTealDark:    rgb(100,210,255);
    --systemYellowDark:  rgb(255,214,10);

    --text-base: 0,0,0;
    --text: rgba(var(--text-base),1);

    --background-base: 0,0,0;
    --background: rgba(var(--background-base), 1);

    --font: '';

    --border-radius: 50px;

    &[theme-style='retro'] {
        --theme-style: 'retro';
        --font: 'pxplus_ibm_vga8regular';
        --border-radius: 0px; }

    &[theme-style='modern'] {
        --theme-style: 'modern';
        --border-radius: 50px;
        --font: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif; } }

html[theme="light"] {

    --primary:   var(--chromecolor-light0);
    --secondary: var(--chromecolor-light1);
    --third:     var(--chromecolor-light2);

    --systemBlue:   var(--systemBlueLight);
    --systemGray:   var(--systemGrayLight);
    --systemGreen:  var(--systemGreenLight);
    --systemIndigo: var(--systemIndigoLight);
    --systemOrange: var(--systemOrangeLight);
    --systemPink:   var(--systemPinkLight);
    --systemPurple: var(--systemPurpleLight);
    --systemRed:    var(--systemRedLight);
    --systemTeal:   var(--systemTealLight);
    --systemYellow: var(--systemYellowLight);

    --background-color--selection: yellow;

    &[theme-style='retro'] {
        --text-base:        var(--text--color-base--light);
        --background-base:  var(--background--color-base--light); }

    &[theme-style='modern'] {
        --text-base: 54, 54, 54;
        --background-base: var(--primary); } }

html[theme='dark'] {

    --primary:   var(--chromecolor-dark0);
    --secondary: var(--chromecolor-dark1);
    --third:     var(--chromecolor-dark2);

    --systemBlue:   var(--systemBlueDark);
    --systemGray:   var(--systemGrayDark);
    --systemGreen:  var(--systemGreenDark);
    --systemIndigo: var(--systemIndigoDark);
    --systemOrange: var(--systemOrangeDark);
    --systemPink:   var(--systemPinkDark);
    --systemPurple: var(--systemPurpleDark);
    --systemRed:    var(--systemRedDark);
    --systemTeal:   var(--systemTealDark);
    --systemYellow: var(--systemYellowDark);

    --background-color--selection: red;

    &[theme-style='retro'] {
        --text-base:       var(--text--color-base--dark);
        --background-base: var(--background--color-base--dark); }

    &[theme-style='modern'] {
        --text-base: 200,200,200;
        --background: var(--primary); } }

html {
    margin: 0;
    padding: 0;
    line-height: 1.5;
    font-size: 1.25em;
    font-family: var(--font);
    color: var(--text);
    background: var(--background); }

::selection {
    color: var(--text-color--selection);
    background-color: var(--background-color--selection); }

a {
    color: var(--text);
    opacity: 0.8;
    text-decoration: none;
    font-family: var(--font);
    &:hover {
        color: var(--text);
        text-decoration: none;
        opacity: 1.0; } }

ul {
    padding-inline-start: 10px; }

li {
    list-style-type: none;
    margin-top: 10px;
    margin-bottom: 10px; }

.badge {
    display: inline-block;
    margin-left: 10px;
    background: var(--third);
    padding: 5px;
    padding-top: 1px;
    padding-bottom: 1px;
    border-radius: var(--border-radius);
    font-size: 0.75em; }

.badge--item {
    margin-left: 5px;
    margin-right: 5px; }

.box {
    border-radius: 30px; }

$box-shadow: 0 0.5em 1em -0.125em rgba($scheme-invert, 0.1), 0 0px 0 1px rgba($scheme-invert, 0.02) !default;
$box-padding: 1.25rem !default;

$box-link-hover-shadow: 0 0.5em 1em -0.125em rgba($scheme-invert, 0.1), 0 0 0 1px $link !default;
$box-link-active-shadow: inset 0 1px 2px rgba($scheme-invert, 0.2), 0 0 0 1px $link !default;

.button {
    &:hover,&:focus, &:active {
        outline: 0; } }

html[theme="light"] {
    background: var(--secondary);
    .button {
        color: var(--text);
        background: var(--primary);
        border: none;
        box-shadow: $box-shadow; } }


html[theme="dark"] {
    .title {
        color: var(--text); }
    .subtitle {
        color: var(--text);
        opacity: 0.8; }
    .badge {
        border: 1px solid rgba(var(--text-base), 0.1); }
    .box {
        background: var(--secondary);
        border: 1px solid rgba(var(--text-base), 0.1); }
    .button {
        color: var(--text);
        background: var(--third);
        border-color: rgba(var(--text-base), 0.1); } }

.section {
    padding-top: 20px; }

.has-secondary {
    background: var(--secondary); }

.is-fullwidth {
    width: 100%; }

.box-inset {
    border-radius: 30px;
    box-shadow: inset 0 0 10px rgba(10, 10, 10, 0.1);
    padding: 20px; }

.is-semibold {
    font-weight: 500; }
.is-bold {
    font-weight: 700; }
